.funnel-visualization-container table{min-width:450px;table-layout:fixed}.funnel-visualization-container table tbody tr td{border:none}.funnel-visualization-container .step,.funnel-visualization-container table tbody tr td.text-ellipsis{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.funnel-visualization-container .step{max-width:0}.funnel-visualization-container .step .step-name{visibility:hidden;width:inherit;padding:3px 5px;background-color:#fff;border:1px solid;border-radius:3px;position:absolute;z-index:1;white-space:normal;word-wrap:break-word}.funnel-visualization-container .step:hover .step-name{visibility:visible}