.drag-handle{vertical-align:bottom;cursor:move;display:-ms-inline-flexbox;display:inline-flex;-ms-flex-align:stretch;align-items:stretch;-ms-flex-pack:center;justify-content:center}.drag-handle:before{content:"";display:block;width:6px;background:linear-gradient(90deg,transparent,#fff 1px,#fff 2px) 50%,linear-gradient(transparent,#fff 1px,#fff 2px) 50%,#111;background-size:2px 2px}.sortable-container{transition:background-color .2s ease-out;transition-delay:.3s}.sortable-container.sortable-container-dragging{transition-delay:0s;background-color:#f6f8f9}