.color-swatch{display:inline-block;box-sizing:border-box;vertical-align:middle;border-radius:2px;overflow:hidden;width:12px;background-color:transparent;background-image:linear-gradient(45deg,rgba(0,0,0,.1) 25%,transparent 0),linear-gradient(-45deg,rgba(0,0,0,.1) 25%,transparent 0),linear-gradient(45deg,transparent 75%,rgba(0,0,0,.1) 0),linear-gradient(-45deg,transparent 75%,rgba(0,0,0,.1) 0);background-size:12px 12px;background-position:0 0,0 6px,6px -6px,-6px 0}.color-swatch:before{content:"";display:block;padding-top:calc(100% - 2px);background-color:inherit;border:1px solid rgba(0,0,0,.1);border-radius:2px;overflow:hidden}