.jvi-root{display:block;font-family:Menlo,Monaco,Consolas,Courier New,monospace}.jvi-block{display:block;border-left:1px dotted #f0f0f0;margin:0 0 0 2px}.jvi-item{display:block;position:relative;padding:0 0 0 20px;white-space:nowrap}.jvi-item .jvi-item{margin:2px 0}.jvi-item.jvi-nested-last>span>.jvi-punctuation.jvi-comma{display:none}.jvi-toggle{position:absolute;left:0;top:0;width:20px;height:20px;line-height:20px;text-align:center;cursor:pointer;z-index:1;color:#595959;opacity:.5}.jvi-toggle:hover{opacity:.8}.jvi-toggle i{vertical-align:middle}.jvi-toggle.hidden{display:none}.jvi-punctuation{color:#595959}.jvi-punctuation.jvi-string{color:#4caf50}.jvi-punctuation.jvi-braces{margin:0 2px}.jvi-punctuation.jvi-ellipsis{padding:0 2px;cursor:pointer}.jvi-punctuation.jvi-ellipsis:hover{text-decoration:underline}.jvi-punctuation.hidden{display:none}.jvi-value{color:#4caf50}.jvi-value.jvi-primitive{color:#ff9800}.jvi-value.jvi-string{white-space:normal}.jvi-object-key .jvi-punctuation,.jvi-object-key .jvi-value{color:#2196f3}.jvi-comment{color:#828282;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;font-style:italic;margin:0 0 0 4px;opacity:.5}.jvi-comment.hidden{display:none}