.mobile-navbar{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;background:#001529;box-shadow:0 4px 9px -3px rgba(102,136,153,.15);padding:0 15px;height:5vh;transition:top .3s;position:fixed;width:100%;z-index:1000}.mobile-navbar-logo img{height:4vh;width:4vh}.mobile-navbar .mobile-navbar-name{-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;font-size:24px;font-weight:400;color:#f0f8ff;margin-right:auto;padding-left:5vw}.mobile-navbar .hidden{display:none}.mobile-navbar .ant-btn.mobile-navbar-toggle-button{padding:0 10px}.mobile-navbar-menu .ant-dropdown-menu-item{font-weight:500;color:hsla(0,0%,100%,.75)}.mobile-navbar-menu .ant-dropdown-menu-item-divider{background:hsla(0,0%,100%,.5)}