.page-alerts-list{height:100vh;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;overflow:hidden}.auto-pilot-list{padding:20px;max-height:100vh;overflow:scroll}.dashbord_button{position:relative;font-family:inherit;font-size:18px;border-radius:40em;width:8em;height:2.5em;font-size:14px;color:#f7f4f4;z-index:1;overflow:hidden;border:1px solid hsla(0,0%,100%,.18)}.dashbord_button .text{position:absolute;left:0;top:0;width:100%;height:100%;line-height:3em;border:none;background:linear-gradient(hsla(0,0%,100%,.473),hsla(0,0%,59%,.25));z-index:1;backdrop-filter:blur(10px);-webkit-backdrop-filter:blur(10px);display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center}.dashbord_button .text strong{margin-right:3px}.dashbord_button .blob{position:absolute;z-index:-1;border-radius:5em;width:5em;height:3em;transition:transform .3s ease-in-out,background .3s ease-in-out}.dashbord_button .blob:nth-child(2){left:0;top:0;background:#ff930f}.dashbord_button .blob:nth-child(3){left:1.8em;top:0;z-index:-1;background:#bf0fff}.dashbord_button .blob:nth-child(4){left:4em;top:-1em;background:#ff1b6b}.dashbord_button .blob:nth-child(5){left:4.3em;top:1.6em;background:#0061ff}.dashbord_button:hover .blob:nth-child(2){background:#0061ff}.dashbord_button:hover .blob:nth-child(3){background:#ff1b6b}.dashbord_button:hover .blob:nth-child(4){background:#bf0fff}.dashbord_button:hover .blob:nth-child(5){background:#ff930f}.dashbord_button:hover .blob{-ms-transform:scale(1.3);transform:scale(1.3)}.dashbord_button:active{border:2px solid #fff}