.visual-card-list{width:100%;margin:-5px 0 0 -5px}.visual-card{background:#fff;border:1px solid rgba(102,136,153,.15);border-radius:3px;margin:5px;width:212px;padding:15px 5px;cursor:pointer;box-shadow:none;transition:transform .12s ease-out;transition-duration:.3s;transition-property:box-shadow;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center}.visual-card:focus,.visual-card:focus-within,.visual-card:hover{box-shadow:0 4px 9px -3px rgba(102,136,153,.15)}.visual-card img{width:64px!important;height:64px!important;margin-right:5px}.visual-card h3{font-size:13px;color:#323232;margin:0!important;text-overflow:ellipsis;overflow:hidden}@media (max-width:1200px){.visual-card{width:217px}}@media (max-width:755px){.visual-card{width:47%}}@media (max-width:515px){.visual-card{width:47%}.visual-card img{width:48px;height:48px}}@media (max-width:408px){.visual-card{width:100%;padding:5px}.visual-card img{width:48px;height:48px}}